import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { LocalDataSource } from 'ngx-smart-table';
import { BuyerAddData, BuyerModelData } from '../../app/Model/buyer-model-data';
import { SellerModelData } from '../../app/Model/seller-model-data';
import { ToastrService } from 'ngx-toastr';
import { CustomInputEditorComponent } from '../seller/seller.component'
import { EauctionbuyerService } from '../../Services/eauctionbuyer.service';
import { EauctionService } from '../../Services/eauctionseller.service';
import { Validators } from '@angular/forms';

@Component({
  selector: 'app-buyer',
  templateUrl: './buyer.component.html',
  styleUrls: ['./buyer.component.css']
})

export class BuyerComponent implements OnInit {
  constructor(private http: HttpClient,
    private toastr: ToastrService,
    private eauctionservice: EauctionService,
    private eauctionbuyerService: EauctionbuyerService) { }

  ngOnInit() {
    this.getSellerRecord();
  }


  sellersource: LocalDataSource;
  sellerclient: SellerModelData[];
  selectedproduct;
  buyersource: LocalDataSource;
  buyerclient: BuyerModelData[];
  apibuyerURL = 'http://localhost:4200/api/EAuctionBuyer';
  apisellerURL = 'http://localhost:4200/api/EAuctionSeller';

  buyersettings = {
    actions: {
      add: true,
      edit: true,
      delete: true,
      custom: [{ name: 'edit', title: '<i class="nb-edit"></i>' }],
      position: 'right',
    },
    columns: {
      buyername: {
        title: 'Buyer Name'
      },
      email: {
        title: 'Email',
        type: 'email'
      },
      mobilenumber: {
        title: 'Mobile No',
        type: 'number',
        editor: {
          type: 'custom',
          component: CustomInputEditorComponent,
        },
      },
      bidamount: {
        title: 'Bid Amount',
        sortDirection: 'desc',
        type: 'number',
        editor: {
          type: 'custom',
          component: CustomInputEditorComponent,
        },
      }
    },
    add: {
      confirmCreate: true
    },
    edit: {
      confirmSave: true
    },
    delete: {
      confirmDelete: true
    }
  };

  selectedddlproduct() {
    this.getBuyerRecord(this.selectedproduct.productname);
  }

  getSellerRecord() {
    this.eauctionservice.getAllSeller()
      .toPromise()
      .then(res => {
        this.sellerclient = res as SellerModelData[];
        this.sellersource = new LocalDataSource(this.sellerclient);
      }
      )
  }

  getBuyerRecord(value: string) {
    this.eauctionbuyerService.getAllBuyer()
      .toPromise()
      .then(res => {
        this.buyerclient = res as BuyerModelData[];
        this.buyersource = new LocalDataSource(this.buyerclient.filter(s => s.productname.match(value)));
      }
      )
  }

  addBuyerRecord(event) {
    if (this.selectedproduct == undefined) {
      alert("Please select a product from dropdown first");
      return;
    }    

    if ((event.newData.buyername == '') ||
      (event.newData.email == '') ||
      (event.newData.mobilenumber == '') ||
      (event.newData.bidamount == '')) {
      alert("Please enter all the fields to proceed..");
      return;
    }

    if (event.newData.buyername.length < 5) {
      alert("Buyer name cannot be less than 5 character");
      return;
    }
    
    if (event.newData.buyername.length > 30) {
      alert("Buyer name cannot be greater than 30 character");
      return;
    }
    
    let regex = new RegExp('[a-z0-9]+@[a-z]+\.[a-z]{2,3}');
    if (!regex.test(event.newData.email)) {
      alert("Invalid email address");
      return;
    }
    
    
    const mobileNumber = event.newData.mobilenumber.toString();
    if (mobileNumber.length > 10 || mobileNumber.length < 10) {
      alert("Mobile number should be 10 digits");
      return;
    }

    //if(this.selectedproduct.bidenddate > formatDate(new Date(),'yyyy-MM-dd','en-US'))
    if (new Date() < this.selectedproduct.bidenddate) {
      alert("Bidding Closed.No Bidding allowed")
      return;
    }
    const buyerdata: BuyerAddData = {
      productname: this.selectedproduct.productname,
      buyername: event.newData.buyername,
      email: event.newData.email,
      mobilenumber: event.newData.mobilenumber,
      bidamount: event.newData.bidamount
    };

    this.eauctionbuyerService.createBuyer(buyerdata)
      .subscribe(
        res => {
          console.log(res);
          this.getBuyerRecord(this.selectedproduct.productname);
          event.confirm.resolve(event.newData);
          this.toastr.success('Record Added Successfuly..', 'Success');
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log("Client-side error occured.");
            this.toastr.error('Client-side error occured.', 'Error');
          } else {
            console.log("Server-side error occured.");
            event.confirm.resolve(event.newData);
            this.getBuyerRecord(this.selectedproduct.productname);
            this.toastr.success('Record Added Successfuly..', 'Success');
          }
        });
  }

  updateBuyerRecord(event) {
    console.log('Updating Seller Record');
    if ((event.newData.buyername == '') ||
      (event.newData.email == '') ||
      (event.newData.mobilenumber == '') ||
      (event.newData.bidamount == '')) {
      alert("Please enter all the fields to proceed..");
      return;
    }

    if (event.newData.buyername.length < 5) {
      alert("Buyer name cannot be less than 5 character");
      return;
    }

    if (event.newData.buyername.length > 30) {
      alert("Buyer name cannot be greater than 30 character");
      return;
    }
    
    let regex = new RegExp('[a-z0-9]+@[a-z]+\.[a-z]{2,3}');
    if (!regex.test(event.newData.email)) {
      alert("Invalid email address");
      return;
    }    
    
    const mobileNumber = event.newData.mobilenumber.toString();
    if (mobileNumber.length > 10 || mobileNumber.length < 10) {
      alert("Mobile number should be 10 digits");
      return;
    }

    const buyerdata: BuyerModelData = {
      id: event.newData.id,
      productname: this.selectedproduct.productname,
      buyername: event.newData.buyername,
      email: event.newData.email,
      mobilenumber: event.newData.mobilenumber,
      bidamount: event.newData.bidamount
    };

    this.eauctionbuyerService.updateBuyer(buyerdata)
      .subscribe(
        res => {
          console.log(res);
          event.confirm.resolve(event.newData);
          this.toastr.info('Record Updated Successfuly..', 'Success');
        });
  }

  deleteBuyerRecord(event) {
    console.log(event.data);
    this.eauctionbuyerService.deleteBuyer(event.data.id)
      .subscribe(
        res => {
          console.log(res);
          event.confirm.resolve(event.source.data);
          this.toastr.warning('Record Deleted Successfuly..', 'Success');
        });
  }
}


