import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { throwError, Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { SellerModelData, SellerAddData } from '../app/Model/seller-model-data'

@Injectable({
  providedIn: 'root'
})
export class EauctionService {
  private apiazuresellerurl='https://sellermicroservice.azurewebsites.net/E-auction/api/v1/Seller';
  private funcappsellerurl='https://sellermicroservice.azurewebsites.net/E-auction/api/v1/Seller';
  //http://localhost:12947/E-auction/api/v1/Seller';
  
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin':'*'
    })
  }

  constructor(private http: HttpClient) { }

  getAllSeller(): Observable<SellerModelData[]> {
    return this.http.get<SellerModelData[]>(this.apiazuresellerurl,this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }

  deleteSeller(id: string) {  
    return this.http.delete<SellerModelData[]>(this.funcappsellerurl +'/'+ id)
    .pipe(
      catchError(this.errorHandler)
    ) 
  }  

  createSeller(sellerModelData: SellerAddData) {  
    return this.http.post(this.apiazuresellerurl, sellerModelData)
    .pipe(
      catchError(this.errorHandler)
    )
  }  

  getSellerById(id: string) {  
    return this.http.get<SellerModelData>(this.apiazuresellerurl + '/' + id)
    .pipe(
      catchError(this.errorHandler)
    )
  }  

  updateSeller(sellerModelData: SellerModelData) {  
    return this.http.put(this.apiazuresellerurl , sellerModelData)
    .pipe(
      catchError(this.errorHandler)
    )
  } 

  errorHandler(error) {
     let errorMessage = '';
     if(error.error instanceof ErrorEvent) {
       errorMessage = error.error.message;
     } else {
       errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
     }
     console.log(errorMessage);
     return throwError(errorMessage);
  }
}
