import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { throwError, Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { BuyerModelData, BuyerAddData } from '../app/Model/buyer-model-data';

@Injectable({
  providedIn: 'root'
})
export class EauctionbuyerService {
  private apiazurebuyerrurl='https://buyermicroservice.azurewebsites.net/E-auction/api/v1/buyer';
  //http://localhost:12946/E-auction/api/v1/buyer';
  
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin':'*'
    })
  }

  constructor(private http: HttpClient) { }

  getAllBuyer(): Observable<BuyerModelData[]> {
    return this.http.get<BuyerModelData[]>(this.apiazurebuyerrurl,this.httpOptions)
    .pipe(
      catchError(this.errorHandler)
    )
  }

  deleteBuyer(id: string) {  
    return this.http.delete<BuyerModelData[]>(this.apiazurebuyerrurl +'/'+ id)
    .pipe(
      catchError(this.errorHandler)
    ) 
  }
  
  createBuyer(sellerModelData: BuyerAddData) {
    return this.http.post(this.apiazurebuyerrurl, sellerModelData)
    .pipe(
     catchError(this.errorHandler)
    )
  }  

  getBuyerById(id: string) {  
    return this.http.get<BuyerModelData>(this.apiazurebuyerrurl + '/' + id)
    .pipe(
      catchError(this.errorHandler)
    )
  }  

  updateBuyer(buyerModelData: BuyerModelData) {  
    return this.http.put(this.apiazurebuyerrurl , buyerModelData)
    .pipe(
      catchError(this.errorHandler)
    )
  } 

  errorHandler(error) {
     let errorMessage = '';
     if(error.error instanceof ErrorEvent) {
       errorMessage = error.error.message;
     } else {
       errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
     }
     console.log(errorMessage);
     return throwError(errorMessage);
  }
}

