import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { LocalDataSource } from 'ngx-smart-table';
import { SellerModelData, SellerAddData } from '../../app/Model/seller-model-data';
import { ToastrService } from 'ngx-toastr';
import { DefaultEditor } from 'ngx-smart-table';
import { SmartTableDatepickerComponent, SmartTableDatepickerRenderComponent }
  from '../smart-table-datepicker/smart-table-datepicker.component'
import { EauctionService } from '../../Services/eauctionseller.service'


@Component({
  selector: 'app-seller',
  templateUrl: './seller.component.html',
  styleUrls: ['./seller.component.css']
})
export class SellerComponent implements OnInit {
  constructor(private http: HttpClient, private toastr: ToastrService,
    private eauctionservice: EauctionService) { }

  ngOnInit() {
    this.getSellerRecord();
  }

  sellersource: LocalDataSource;
  sellerclient: SellerModelData[];

  sellersettings = {
    actions: {
      add: true,
      edit: true,
      delete: true,
      custom: [{ name: 'edit', title: '<i class="nb-edit"></i>' }],
      position: 'right',
    },
    columns: {
      productname: {
        title: 'Product Name'
      },
      shortdesc: {
        title: 'Short Description'
      },
      category: {
        title: 'Category',
        editor: {
          type: 'list',
          config: {
            selectText: 'Select',
            list: [
              { value: 'Paintings', title: 'Paintings' },
              { value: 'Ornaments', title: 'Ornaments' },
              { value: 'Sculptors', title: 'Sculptors' }
            ]
          }
        }
      },
      startingprice: {
        title: 'Starting Price',
        type: 'number',
        editor: {
          type: 'custom',
          component: CustomInputEditorComponent,
        },
      },
      bidenddate: {
        title: 'Bid End Date', type: 'custom',
        renderComponent: SmartTableDatepickerRenderComponent,
        width: '250px',
        editor: {
          type: 'custom',
          component: SmartTableDatepickerComponent,
        }
      }
    },
    add: {
      confirmCreate: true
    },
    edit: {
      confirmSave: true
    },
    delete: {
      confirmDelete: true
    }
  };

  getSellerRecord() {
    this.eauctionservice.getAllSeller().toPromise()
      .then(res => {
        this.sellerclient = res as SellerModelData[];
        this.sellersource = new LocalDataSource(this.sellerclient);
        console.log(this.sellersource);
      }
      )
  }


  addSellerRecord(event) {
    if ((event.newData.productname == '') ||
      (event.newData.shortdesc == '') ||
      (event.newData.category == '') ||
      (event.newData.startingprice == '') ||
      (event.newData.bidenddate == '')) {
      alert("Please enter all the fields to proceed..");
      return;
    }

    if (event.newData.productname.length < 5) {
      alert("Productname name cannot be less than 5 character");
      return;
    }
    
    if (event.newData.productname.length > 30) {
      alert("Productname name cannot be greater than 30 character");
      return;
    }

    const sellerdata: SellerAddData = {
      productname: event.newData.productname,
      shortdesc: event.newData.shortdesc,
      category: event.newData.category,
      startingprice: event.newData.startingprice,
      bidenddate: event.newData.bidenddate
    };

    this.eauctionservice.createSeller(sellerdata)
      .subscribe(
        res => {
          console.log(res);
          event.confirm.resolve(event.newData);
          this.toastr.success('Record Added Successfuly..', 'Success');
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log("Client-side error occured.");
            this.toastr.error('Client-side error occured.', 'Error');
          } else {
            console.log("Server-side error occured.");
            event.confirm.resolve(event.newData);
            this.getSellerRecord();
            this.toastr.success('Record Added Successfuly..', 'Success');
          }
        });
  }

  updateSellerRecord(event) {
    if ((event.newData.productname == '') ||
      (event.newData.shortdesc == '') ||
      (event.newData.category == '') ||
      (event.newData.startingprice == '') ||
      (event.newData.bidenddate == '')) {
      alert("Please enter all the fields to proceed..");
      return;
    }

    console.log('Updating Seller Record');
    const sellerdata: SellerModelData = {
      id: event.newData.id,
      productname: event.newData.productname,
      shortdesc: event.newData.shortdesc,
      category: event.newData.category,
      startingprice: event.newData.startingprice,
      bidenddate: event.newData.bidenddate
    };

    this.eauctionservice.updateSeller(sellerdata)
      .subscribe(
        res => {
          console.log(res);
          event.confirm.resolve(event.newData);
          this.toastr.info('Record Updated Successfuly..', 'Success');
        });
  }

  deleteSellerRecord(event) {
    console.log(event.data);
    this.eauctionservice.deleteSeller(event.data.id)
      .subscribe(
        res => {
          console.log(res);
          event.confirm.resolve(event.source.data);
          this.toastr.warning('Record Deleted Successfuly..', 'Success');
        });
  }
}

@Component({
  selector: 'input-editor',
  template: `
    <input type="number" 
           [(ngModel)]="cell.newValue"
           [name]="cell.getId()"
           [placeholder]="cell.getTitle()"
           [disabled]="!cell.isEditable()"
           (click)="onClick.emit($event)"
           (keydown.enter)="onEdited.emit($event)"
           (keydown.esc)="onStopEditing.emit()">
    `,
})
export class CustomInputEditorComponent extends DefaultEditor {
  constructor() {
    super();
  }
}
